.social-block {
  position: relative;

  &:hover {
    .social-block__list {
      opacity: 1;
      pointer-events: auto;
    }

    .social-block__link {
      @media (orientation: landscape) {
        opacity: 1;
        transform: translateY(0);
      }
    }
  }
}

.social-block__toggler {
  @include btn-reset;
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: $c-purple;
  border-radius: 50%;

  @media (max-width: $tablet) and (orientation: portrait) {
    display: none;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    display: none;
  }

  &::before {
    @include center;
    content: "";
    width: 0;
    height: 0;
    border-radius: 50%;
    background-color: $c-light;
    border: none;
    z-index: -1;
    transition:
      width 0.4s cubic-bezier(0.43, 0.63, 0.45, 1.5) 0.2s,
      height 0.4s cubic-bezier(0.43, 0.63, 0.45, 1.5) 0.2s,
      border 0.1s ease 0.2s;

    .page-is-loaded & {
      width: 100%;
      height: 100%;
      border: 2px solid $c-light;
    }
  }

  svg {
    @include center;
    display: block;
    width: 2.3rem;
    height: 2.2rem;
  }

  &:hover,
  .social-block--active & {
    color: $c-purple;

    &::before {
      transition: width 0.2s ease, height 0.2s ease;
      width: 112.5%;
      height: 112.5%;
    }
  }

  &:focus {
    color: $c-purple;
    outline: none;

    &::before {
      border-color: $c-purple;
    }

    & + .social-block__list {
      pointer-events: auto;
    }
  }

  &:active,
  &:active:focus {
    color: $c-light;

    &::before {
      width: 100%;
      height: 100%;
      background-color: $c-purple;
      border-color: $c-purple;
    }
  }
}

.social-block__list {
  @include list-reset;
  position: absolute;
  top: 100%;
  right: 0;
  margin-top: 1.5rem;
  pointer-events: none;

  @media (min-width: $stop-scaling) {
    margin-top: 15px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    pointer-events: auto;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    pointer-events: auto;
  }

  @media (min-width: $mobile) and (orientation: landscape) {
    opacity: 0;
    transition: opacity 0.15s ease-out;
  }

  &::before {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 0;
    right: 0;
    height: 1.5rem;

    @media (min-width: $stop-scaling) {
      height: 15px;
    }
  }

  &:hover {
    opacity: 1;
    pointer-events: auto;
  }

  &:focus-within {
    pointer-events: auto;
  }

  .social-block--active & {
    opacity: 1;
    pointer-events: auto;
  }

  li {
    flex: none;
    margin-bottom: 0.5rem;

    @media (min-width: $stop-scaling) {
      margin-bottom: 5px;
    }

    @media (max-width: $tablet) and (orientation: portrait) {
      margin: 0 1.25rem;
    }

    @media (max-width: $mobile) and (orientation: landscape) {
      margin: 0 1.25rem;
    }
  }
}

.social-block__link {
  position: relative;
  display: block;
  width: 4rem;
  height: 4rem;
  color: $c-light;
  border: 2px solid transparent;
  border-radius: 50%;

  @media (min-width: $stop-scaling) {
    width: 40px;
    height: 40px;
  }

  @media (max-width: $tablet) and (orientation: portrait) {
    color: $c-light;
    background-color: $c-dark;
  }

  @media (max-width: $mobile) and (orientation: landscape) {
    color: $c-light;
    background-color: $c-dark;
  }

  @media (min-width: $mobile) and (orientation: landscape) {
    opacity: 0;
    transform: translateY(10px);
  }

  &:hover {
    color: $c-purple;
  }

  &:focus {
    color: $c-purple;
    background-color: $c-light;
    border-color: $c-purple;
    outline: none;
  }

  &:active,
  &:active:focus {
    color: $c-purple;
    background-color: $c-light;
    border-color: $c-light;
  }

  svg {
    @include center;
  }

  &--fb {
    transition: opacity 0.2s ease-in 0s, transform 0.2s ease-in 0s;

    svg {
      width: 0.8rem;
      height: 1.7rem;

      @media (min-width: $stop-scaling) {
        width: 8px;
        height: 17px;
      }
    }
  }

  &--insta {
    transition: opacity 0.2s ease-in 0.1s, transform 0.2s ease-in 0.1s;

    svg {
      width: 1.5rem;
      height: 1.5rem;

      @media (min-width: $stop-scaling) {
        width: 15px;
        height: 15px;
      }
    }
  }

  &--vk {
    transition: opacity 0.2s ease-in 0.2s, transform 0.2s ease-in 0.2s;

    svg {
      width: 1.7rem;
      height: 1rem;

      @media (min-width: $stop-scaling) {
        width: 10px;
        height: 10px;
      }
    }
  }
}

@media (min-width: $mobile) and (orientation: landscape) {
  body[data-color-theme="purple"] {
    .social-block__toggler {
      color: $c-dark;

      &:hover {
        color: $c-purple;
      }

      &:focus {
        color: $c-purple;

        &::before {
          border-color: currentColor;
        }
      }

      &:active,
      &:active:focus {
        color: $c-light;
        background-color: $c-purple;

        &::before {
          border-color: $c-purple;
        }
      }
    }

    .social-block__link {
      &:hover {
        color: $c-purple;
      }

      &:focus {
        color: $c-purple;
        border-color: $c-purple;
      }

      &:active {
        color: $c-purple;
        background-color: $c-light;
        border-color: $c-light;
      }
    }
  }

  body[data-color-theme="dark-purple"] {
    .social-block__toggler {
      color: $c-purple;

      &:hover {
        color: $c-dark;
      }

      &:focus {
        color: $c-dark;

        &::before {
          border-color: currentColor;
        }
      }

      &:active {
        color: $c-light;
        background-color: $c-dark;

        &::before {
          border-color: $c-dark;
        }
      }
    }

    .social-block__link {
      &:hover {
        color: $c-dark;
      }

      &:focus {
        color: $c-dark;
        border-color: $c-dark;
      }

      &:active {
        color: $c-dark;
        background-color: $c-light;
        border-color: $c-light;
      }
    }
  }

  body[data-color-theme="bright-blue"] {
    .social-block__toggler {
      color: $c-soft-blue;

      &:hover {
        color: $c-bright-blue;
      }

      &:focus {
        color: $c-bright-blue;

        &::before {
          border-color: currentColor;
        }
      }

      &:active {
        color: $c-light;
        background-color: $c-bright-blue;

        &::before {
          border-color: $c-bright-blue;
        }
      }
    }

    .social-block__link {
      &:hover {
        color: $c-bright-blue;
      }

      &:focus {
        color: $c-bright-blue;
        border-color: $c-bright-blue;
      }

      &:active {
        color: $c-bright-blue;
        background-color: $c-light;
        border-color: $c-light;
      }
    }
  }

  body[data-color-theme="soft-blue"] {
    .social-block__toggler {
      color: $c-bright-blue;

      &:hover {
        color: $c-soft-blue;
      }

      &:focus {
        color: $c-soft-blue;

        &::before {
          border-color: currentColor;
        }
      }

      &:active {
        color: $c-light;
        background-color: $c-soft-blue;

        &::before {
          border-color: $c-soft-blue;
        }
      }
    }

    .social-block__link {
      &:hover {
        color: $c-soft-blue;
      }

      &:focus {
        color: $c-soft-blue;
        border-color: $c-soft-blue;
      }

      &:active {
        color: $c-soft-blue;
        background-color: $c-light;
        border-color: $c-light;
      }
    }
  }
}
